import {PhotosType, ProfileType} from "../types/types";
import {DefaultResponseType, instance} from "./api";

export const profileAPI = {
    getProfile(userId: number) {
        return instance.get<ProfileType>(`profile/${userId}`);
    },
    getStatus(userId: number) {
        return instance.get<string>(`profile/status/${userId}`).then(res => res.data)
    },
    updateStatus(status: string) {
        return instance.put<DefaultResponseType>(`profile/status`, {status}).then(res => res.data)
    },
    updatePhoto(photo: string) {
        const formData = new FormData();
        formData.append('image', photo)
        return instance.put<DefaultResponseType<PhotosType>>(`profile/photo`, formData).then(res => res.data)
    },
    updateProfile(profileData: ProfileType) {
        return instance.put<DefaultResponseType>(`profile`, profileData).then(res => res.data)
    }
}