import {getAuthUserThunkCreator} from "./auth_reducer";
import {InferActionsTypes} from "../redux_store";


type ACTIONS = InferActionsTypes<typeof actions>

let initialState = {
    initialized: false,
}


export type initialStateType = typeof initialState;

const appReducer = (state = initialState, action : ACTIONS) : initialStateType => {
    switch (action.type) {
        case "app/INITIALIZED_SUCCESS":
            return {
                ...state,
                initialized: true,
            }

        default:
            return state;
    }
}


export const actions = {
    setInitializedSuccessAC: () => ({type: 'app/INITIALIZED_SUCCESS'} as const)
}
export const {setInitializedSuccessAC} = actions

export const setInitializedSuccess = () => (dispatch:any) => {
    let promise = dispatch(getAuthUserThunkCreator())
    Promise.all([promise])
      .then(() => {
            dispatch(setInitializedSuccessAC())
        })
}


export default appReducer;