import {ResultCodeEnum} from "../../api/api";
import {stopSubmit} from "redux-form";
import {PhotosType, PostType, ProfileType} from "../../types/types";
import {profileAPI} from "../../api/profileAPI";
import {BaseThunkType, InferActionsTypes} from "../redux_store";

let initialState = {
    posts: [
        {id: 1, message: "Hello!", likesCount: 12},
        {id: 2, message: "Hello! AWESOME!", likesCount: 14},
        {id: 3, message: "Hello! WONDERFULL!", likesCount: 15},
        {id: 4, message: "Hello! BEAUTIFULL!", likesCount: 12},
        {id: 5, message: "Hello! COOL!", likesCount: 111},
        {id: 6, message: "Hello! WELL!", likesCount: 152},
    ] as Array<PostType>,
    profile: null as ProfileType | null,
    status: '' as string | null,
}
export type InitialStateProfileType = typeof initialState;
type ACTIONS = InferActionsTypes<typeof actions>
export type ProfileThunkType = BaseThunkType<ACTIONS | ReturnType<typeof stopSubmit>>

const profileReducer = (state = initialState, action: ACTIONS): InitialStateProfileType => {

    switch (action.type) {
        case 'profile/ADD_POST':
            let newPost = {
                id: (state.posts.length + 1),
                message: action.payload.postText,
                likesCount: 0,
            };
            if (newPost.message === undefined) {
                return state;
            }
            return {
                ...state,
                posts: [...state.posts, newPost],
            }
        case 'profile/DELETE_POST':
            return {
                ...state,
                posts: state.posts.filter(p => p.id !== action.payload.postId),
            }

        case 'profile/SET_USER_PROFILE':
            return {
                ...state,
                profile: action.payload.profile,

            }
        case 'profile/OPEN_URL':
            let url = action.payload.url;
            if (url.includes('https://', 0) || url.includes('http://', 0)) {
                window.open(url, "_blank") //to open new page
            } else {
                url = 'http://' + url;
                window.open(url, "_blank") //to open new page
            }
            return {
                ...state,
            }
        case 'profile/SET_STATUS':
            return {
                ...state,
                status: action.payload.status,
            }
        case 'profile/UPDATE_STATUS':
            return {
                ...state,
                status: action.payload.status,
            }
        case 'profile/UPDATE_PROFILE_PHOTO': {
            return {
                ...state,
                profile: {...state.profile, photos: action.payload.photos} as ProfileType
            }
        }
        case 'profile/UPDATE_PROFILE_DATA': {
            return {
                ...state,
                profile: {
                    ...state.profile, ...action.payload.profileData,
                    contacts: action.payload.profileData.contacts
                }
            }
        }
        default:
            return state;
    }
}

export const actions = {
    addPost: (postText: string) => ({type: 'profile/ADD_POST', payload: {postText}} as const),
    deletePost: (postId: number) => ({type: 'profile/DELETE_POST', payload: {postId}} as const),
    setUserProfile: (profile: ProfileType) => ({type: 'profile/SET_USER_PROFILE', payload: {profile}} as const),
    openUrl: (url: string) => ({type: 'profile/OPEN_URL', payload: {url}} as const),
    setStatus: (status: string) => ({type: 'profile/SET_STATUS', payload: {status}} as const),
    updateStatus: (status: string) => ({type: 'profile/UPDATE_STATUS', payload: {status}} as const),
    updatePhoto: (photos: PhotosType) => ({
        type: 'profile/UPDATE_PROFILE_PHOTO',
        payload: {photos: {...photos}}
    } as const),
    updateProfile: (profileData: ProfileType) => ({
        type: 'profile/UPDATE_PROFILE_DATA',
        payload: {profileData}
    } as const)
}
export const {addPost, openUrl, deletePost} = actions


export const getUserProfileThunkCreator = (userId: number): ProfileThunkType => async (dispatch) => {
    let response = await profileAPI.getProfile(userId)
    dispatch(actions.setUserProfile(response.data))
}
export const getStatusProfileThunkCreator = (userId: number): ProfileThunkType => async (dispatch) => {
    let response = await profileAPI.getStatus(userId)
    dispatch(actions.setStatus(response))
}

export const updateStatusProfileThunkCreator = (status: string): ProfileThunkType => async (dispatch) => {
    let response = await profileAPI.updateStatus(status)
    if (response.resultCode === ResultCodeEnum.Success) {
        dispatch(actions.updateStatus(status))
    }
}

export const updateProfilePhotoThunkCreator = (photoFile: string): ProfileThunkType => async (dispatch) => {
    let response = await profileAPI.updatePhoto(photoFile)
    if (response.resultCode === ResultCodeEnum.Success) {
        dispatch(actions.updatePhoto(response.data))
    }
}

export const updateProfileDataThunkCreator = (profileData: ProfileType): ProfileThunkType => async (dispatch) => {
    let response = await profileAPI.updateProfile(profileData)
    if (response.resultCode === ResultCodeEnum.Success) {
        dispatch(actions.updateProfile(profileData))
    } else {
        let errorsMessages: any = {}
        const errorsMessagesLength = response.messages.length;
        let errorMessage;
        if (errorsMessagesLength > 0) {
            for (let i = 0; i < errorsMessagesLength; i++) {
                errorMessage = response.messages[i];
                let errorObject = errorMessage.slice(errorMessage.search('->')).slice(2, -1).toLowerCase();
                if (errorObject === 'mainlink') errorObject = 'mainLink'
                errorsMessages[errorObject] = errorMessage
            }
        } else {
            errorMessage = 'Some error';
        }
        dispatch(stopSubmit('profileData', {"contacts": {...errorsMessages}}))
        return Promise.reject(errorsMessages)
    }
}

export default profileReducer;