import React, {FC, useEffect} from 'react';
import './App.css';
import {BrowserRouter, Link, Redirect, Route, Switch} from "react-router-dom";
import {setInitializedSuccess} from "./redux/reducers/app_reducer";
import Preloader from "./components/common/Preloader/Preloader";
import WithSuspenseComponent from "./redux/hoc/withSuspenseComponent";
import 'antd/dist/antd.css';
import {Breadcrumb, Layout, Menu} from "antd";
import {LaptopOutlined, NotificationOutlined, UserOutlined} from '@ant-design/icons';
import Settings from "./components/Content/Settings/Settings";
import News from "./components/Content/News/News";
import Music from "./components/Content/Music/Music";
import Users from "./components/Content/Users/Users";
import LoginPage from "./components/Content/Login/LoginPage";
import {AppStateType} from "./redux/redux_store";
import {useDispatch, useSelector} from "react-redux";
import AppHeader from "./components/Header/Header";
import ChatPage from "./components/pages/Chat/ChatPage";

const {SubMenu} = Menu;
const {Content, Footer, Sider} = Layout;

const DialogsContainer = React.lazy(() => import("./components/Content/Dialogs/DialogsContainer"));
const ProfileContainer = React.lazy(() => import("./components/Content/Profile/ProfileContainer"));



const SuspendedDialogs = WithSuspenseComponent(DialogsContainer)
const SuspendedProfile = WithSuspenseComponent(ProfileContainer)

export const App: FC = () => {
    const dispatch = useDispatch()
    const initialized = useSelector((state: AppStateType) => state.app.initialized)
    useEffect(() => {
        dispatch(setInitializedSuccess());
    }, [])


    if (!initialized) {
        return <Preloader/>
    }
    return (
        <BrowserRouter>
            <Layout>
                <AppHeader/>
                <Content style={{padding: '0 50px'}}>
                    <Breadcrumb style={{margin: '16px 0'}}>
                        <Breadcrumb.Item>Home</Breadcrumb.Item>
                        <Breadcrumb.Item>List</Breadcrumb.Item>
                        <Breadcrumb.Item>App</Breadcrumb.Item>
                    </Breadcrumb>
                    <Layout className="site-layout-background" style={{padding: '24px 0'}}>
                        <Sider className="site-layout-background" width={200}>
                            <Menu
                                mode="inline"
                                // defaultSelectedKeys={['1']}
                                // defaultOpenKeys={['sub1']}
                                style={{height: '100%'}}
                            >
                                <SubMenu key="sub1" icon={<UserOutlined/>} title="Profile">
                                    <Menu.Item key="1">
                                        <Link to='/profile'>Profile</Link>
                                    </Menu.Item>
                                    <Menu.Item key="2">
                                        <Link to='/dialogs'>Dialogs</Link>
                                    </Menu.Item>
                                    <Menu.Item key="3">
                                        <Link to='/chat'>Chat</Link>
                                    </Menu.Item>
                                </SubMenu>
                                <SubMenu key="sub2" icon={<LaptopOutlined/>} title="Developers">
                                    <Menu.Item key="4">
                                        <Link to='/users'>Users</Link>
                                    </Menu.Item>

                                </SubMenu>
                                <SubMenu key="sub3" icon={<NotificationOutlined/>} title="Entertainment">
                                    <Menu.Item key="5">
                                        <Link to='/news'>News</Link>
                                    </Menu.Item>
                                    <Menu.Item key="6">
                                        <Link to='/music'>Music</Link>
                                    </Menu.Item>
                                </SubMenu>
                            </Menu>
                        </Sider>
                        <Content style={{padding: '0 24px', minHeight: 280}}>
                            <Switch>
                                <Route exact path="/" render={() => <Redirect to={'/profile'}/>}/>
                                <Route path="/dialogs" render={() => <SuspendedDialogs/>}/>
                                <Route path="/profile/:userId?" render={() => <SuspendedProfile/>}/>
                                <Route path='/settings' component={Settings}/>
                                <Route path='/news' component={News}/>
                                <Route path='/music' component={Music}/>
                                <Route path='/users' render={() => <Users/>}/>
                                <Route path='/login' render={() => <LoginPage/>}/>
                                <Route path='/chat' render={() => <ChatPage/>}/>
                                <Route path='*' render={() => <div>404 NOT FOUND</div>}/>
                            </Switch>
                        </Content>
                    </Layout>
                </Content>
                <Footer style={{textAlign: 'center'}}>Social network ©2022 Created by Melnikov</Footer>
            </Layout>
        </BrowserRouter>
    );
}

export default App