import React, {FC, useEffect} from "react";
import s from './Users.module.css';
import User from "./User/User";
import {Paginator} from "./Paginator/Paginator";
import {ErrorMessage, Field, Form, Formik} from "formik";
import {FilterType, getUsersThunkCreator} from "../../../redux/reducers/users_reducer";
import {useDispatch, useSelector} from "react-redux";
import {
    getCurrentPage,
    getFilter,
    getFollowingProgress,
    getIsLoading,
    getPageSize,
    getTotalUsersCount,
    getUsers,
    isAuthSelector
} from "../../../redux/reducers/users_selectors";
import Preloader from "../../common/Preloader/Preloader";
import {useHistory} from "react-router-dom";
import * as queryString from "querystring";
import {ParsedUrlQuery} from "querystring";

type PropsType = {
    // totalUsersCount: number
    // pageSize: number
    // currentPage: number
    // isAuth: boolean
    // followingProgress: Array<number>
    // users: Array<UsersType>
    // follow: (userId: number) => void
    // unfollow: (userId: number) => void
    // onPageChanged: (currentPage: number) => void
    // onFilterChanged: (filter: FilterType) => void

}
const Users: FC<PropsType> = (props) => {
    const dispatch = useDispatch()
    const users = useSelector(getUsers)
    const pageSize = useSelector(getPageSize)
    const totalUsersCount = useSelector(getTotalUsersCount)
    const currentPage = useSelector(getCurrentPage)
    const isLoading = useSelector(getIsLoading)
    const followingProgress = useSelector(getFollowingProgress)
    const isAuth = useSelector(isAuthSelector)
    const filter = useSelector(getFilter)

    const history = useHistory()
    const parseUrl = queryString.parse(history.location.search.slice(1)) as {term: string; friend: string; page: string}

    let actualFilter = filter
    let actualPage = currentPage

    if(!!parseUrl.page) actualPage = Number(parseUrl.page)
    if(!!parseUrl.term) actualFilter = {...actualFilter, term: parseUrl.term}
    if(!!parseUrl.friend) actualFilter = {...actualFilter, friend: parseUrl.friend === 'true' ? true : false}



    useEffect(() => {
        dispatch(getUsersThunkCreator(actualPage, actualFilter, pageSize))
    }, [])

    return (<>
            {isLoading ? <Preloader/> : null}
            <div className={s.users}>
                <UsersSearchForm term={actualFilter.term} friend={actualFilter.friend}  pageSize={pageSize}/>
                <Paginator totalUsersCount={totalUsersCount}
                           pageSize={pageSize}
                           currentPage={currentPage}
                           filter={filter}
                />
                {
                    users.map(u => <User
                        user={u}
                        followingProgress={followingProgress}
                        isAuth={isAuth}
                    />)
                }
            </div>
        </>
    )
}
export default Users;

type FilterPropsType = {
    pageSize: number
    term: string | null
    friend: boolean | null
}

const UsersSearchFromValidate = (values: any) => {
    const errors: any = {term: ''};
    if (!values.term) {
        errors.term = 'Required';
    } else if (
        !/^[A-Z0-9]$/i.test(values.term)
    ) {
        errors.term = 'Invalid search text';
    }
    return errors;
}
export const createSearchFilterUrl = (filter: FilterType, page = 1) => {
    let friendFilter = String(filter.friend)
    let searchHistoryUrl = `page=${page}`
    if(friendFilter !== 'null' && filter.term) {
        searchHistoryUrl = `page=${page}&friend=${friendFilter}&term=${filter.term}`
    }
    if(friendFilter === 'null' && filter.term) {
        searchHistoryUrl = `page=${page}&term=${filter.term}`
    }
    if(friendFilter !== 'null' && !filter.term) {
        searchHistoryUrl = `page=${page}&friend=${friendFilter}`
    }
    return searchHistoryUrl
}

const UsersSearchForm: FC<FilterPropsType> = (props) => {
    const dispatch = useDispatch()
    const history = useHistory()
    const onFilterChanged = (filter: FilterType) => {
        dispatch(getUsersThunkCreator(1, filter, props.pageSize))
        history.push({
            pathname: 'users',
            search: createSearchFilterUrl(filter, 1)
        })
    }
    const initialValues: FilterType = {term: props.term, friend: props.friend}
    return (
        <div>
            <h1>Самураи</h1>
            <Formik
                initialValues={initialValues}
                onSubmit={(values: FilterType, {setSubmitting}) => {
                    onFilterChanged(values)
                }}
            >
                {({isSubmitting}) => (
                    <Form>
                        <Field type="text" name="term"/>
                        <ErrorMessage name="term" component="div"/>
                        <label htmlFor="friend">Friend?</label>
                        <Field component="select"
                               id="friend"
                               name="friend">
                            <option value="null">All</option>
                            <option value="true">Friends</option>
                            <option value="false">Not friends</option>
                        </Field>
                        <button type="submit">
                            Find
                        </button>
                    </Form>
                )}
            </Formik>
        </div>
    )
}
