import React, {FC, useEffect, useRef, useState} from 'react';
import TextArea from "antd/es/input/TextArea";
import {Avatar, Button} from "antd";
import style from './ChatPage.module.css'
import {UserOutlined} from "@ant-design/icons";
import {useDispatch, useSelector} from "react-redux";
import {sendMessageThunk, startMessagesListening, stopMessagesListening} from "../../../redux/reducers/chat_reducer";
import {AppStateType} from "../../../redux/redux_store";


export type MessageTypeAPI = {
    message: string
    photo: string
    userId: number
    userName: string
}
export type MessageType = MessageTypeAPI & {id: string}

const ChatPage: FC = () => {

    return (
        <Chat/>
    );
};
const Chat: FC = React.memo(() => {
    // const [wsChanel, setWsChanel] = useState<WebSocket | null>(null)
    // const [readyStatus, setReadyStatus] = useState<'pending' | 'open'>('pending')
    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(startMessagesListening())
        return () => {
            dispatch(stopMessagesListening()) // Нужно ли???
        }
    }, [])
    return (
        <div>
            <Messages/>
            <AddMessageForm/>
        </div>
    )
})

const Messages: FC<{}> = React.memo(({}) => {

    const messages = useSelector((state: AppStateType) => state.chat.messages)
    const messageAnchorRef = useRef<HTMLDivElement>(null)
    const [isAutoScrolling, setIsAutoScrolling] = useState(true)
    const onScrollHandler = (e: React.UIEvent<HTMLDivElement, UIEvent>) => {
        const myDiv = e.currentTarget
        if (myDiv.offsetHeight + myDiv.scrollTop >= myDiv.scrollHeight) {
            !isAutoScrolling && setIsAutoScrolling(true)
        } else {
            isAutoScrolling && setIsAutoScrolling(false)
        }
    }

    useEffect(() => {
        if(isAutoScrolling) {
            messageAnchorRef?.current?.scrollIntoView({behavior: "smooth", block: 'end'})
        }
    }, [messages])

    return (
        <div style={{height: "400px", overflowY: 'auto'}} onScroll={onScrollHandler}>
            {messages.map((m, index) => <Message key={m.id} message={m}/>)}
            <div ref={messageAnchorRef}></div>
        </div>
    )
})

const Message: FC<{ message: MessageType }> = React.memo(({message}) => {
    console.log('>>>>>>>> message');

    return (
        <div>
            {message.photo ? <img style={{width: '30px', marginRight: '5px'}} src={message.photo}/> :
                <Avatar style={{backgroundColor: '#87d068', width: '30px', marginRight: '5px'}}
                        icon={<UserOutlined/>}/>}
            <b>{message.userName}</b>
            <div>{message.message}</div>
            <hr/>
        </div>
    );
})

type AddMessageFormType = {
    ws: WebSocket | null
    readyStatus: 'open' | 'pending'
    setReadyStatus: (status: 'open' | 'pending') => void
}

const AddMessageForm: FC<{}> = React.memo(({}) => {
    const [newMessage, setNewMessage] = useState('')
    const readyStatus = useSelector((state: AppStateType) => state.chat.status)
    const dispatch = useDispatch()
    const onSendMessage = () => {
        if (newMessage) {
            dispatch(sendMessageThunk(newMessage))
            setNewMessage('')
        }
    }

    return (
        <div className={style.addMessageForm}>
            <TextArea value={newMessage} onChange={e => setNewMessage(e.target.value)}
                      placeholder={'Input message...'}/>
            <Button disabled={readyStatus !== 'ready'} onClick={onSendMessage} type={'primary'}>Add</Button>
        </div>
    )
})


export default ChatPage