import {BaseThunkType, InferActionsTypes} from "../redux_store";
import {MessageType} from "../../components/pages/Chat/ChatPage";
import {chatAPI, WSSStatusType} from "../../api/chat-api";
import {Dispatch} from "redux";
import uuid from 'uuid'

type ACTIONS = InferActionsTypes<typeof actions>

const initialState = {
    messages: [] as MessageType[],
    status: 'pending' as WSSStatusType,
}
export type InitialStateChatType = typeof initialState;

export const chatReducer = (state = initialState, action: ACTIONS): InitialStateChatType => {
    switch (action.type) {
        case "chat/MESSAGES_RECEIVED":
            return {
                ...state, messages: [...state.messages, ...action.payload.messages.map(m => ({...m, id: uuid.v1()}))].filter((el, index, array) => index >= array.length - 50),
            }
        case "chat/STATUS_CHANGED":
            return {
                ...state, status: action.payload.status,
            }
        default:
            return state
    }
}

export const actions = {
    messagesReceived: (messages: MessageType[]) => ({type: 'chat/MESSAGES_RECEIVED', payload: {messages}} as const),
    statusChanged: (status: WSSStatusType) => ({type: 'chat/STATUS_CHANGED', payload: {status}} as const)
}
export type ChatThunkType = BaseThunkType<ACTIONS>

let _newMessageHandler: ((messages: MessageType[]) => void) | null = null
const newMessageHandlerCreator = (dispatch: Dispatch) => {
    if (_newMessageHandler === null) {
        _newMessageHandler = (messages) => {
            dispatch(actions.messagesReceived(messages))
        }
    }
    return _newMessageHandler
}

let _onStatusChanged: ((status: WSSStatusType) => void) | null = null
const onStatusChangedCreator = (dispatch: Dispatch) => {
    if (_onStatusChanged === null) {
        _onStatusChanged = (status) => {
              dispatch(actions.statusChanged(status))
        }
    }
    return _onStatusChanged
}

export const startMessagesListening = (): ChatThunkType => async (dispatch) => {
    chatAPI.start()
    chatAPI.subscribe('messages-received', newMessageHandlerCreator(dispatch))
    chatAPI.subscribe('event-changed', onStatusChangedCreator(dispatch))
}

export const stopMessagesListening = (): ChatThunkType => async (dispatch) => {
    chatAPI.stop()
    chatAPI.unsubscribe('messages-received', newMessageHandlerCreator(dispatch))
    chatAPI.unsubscribe('event-changed', onStatusChangedCreator(dispatch))
}

export const sendMessageThunk = (message: string): ChatThunkType => async (dispatch) => {
    chatAPI.sendMessage(message)
}


export default chatReducer;