import s from "./Paginator.module.css"
import React, {FC, useState} from "react";
import arrowRight from '../../../../assets/icons/arrowRight.png'
import arrowLeft from '../../../../assets/icons/arrowLeft.png'
import {useDispatch} from "react-redux";
import {FilterType, getUsersThunkCreator} from "../../../../redux/reducers/users_reducer";
import {useHistory} from "react-router-dom";
import {createSearchFilterUrl} from "../Users";

type PropsType = {
    totalUsersCount: number
    pageSize: number
    // onPageChanged: (numberPage: number) => void
    currentPage?: number
    portionSize?: number
    filter: FilterType
}

export const Paginator:FC<PropsType> = ({totalUsersCount, pageSize, currentPage = 1, filter, portionSize = 10}) => {
    const dispatch = useDispatch()
    const history = useHistory()
    const onPageChanged = (pageNumber: number) => {
        dispatch(getUsersThunkCreator(pageNumber, filter, pageSize))
        history.push({
            pathname: 'users',
            search: createSearchFilterUrl(filter, pageNumber)
        })
    }


    let pagesCount = Math.ceil(totalUsersCount / pageSize);
    let pages: Array<number> = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }

    let portionsCount = Math.ceil(pagesCount / portionSize)
    let [portionNumber, setPortionNumber] = useState(1)
    let leftBorderPortion = (portionNumber - 1) * portionSize + 1
    let rightBorderPortion = portionNumber * portionSize;
    return (
        <div className={s.pages}>
            {
                portionNumber > 1 && <span className={s.arrowsButton} onClick={() => {
                    setPortionNumber(portionNumber - 1)
                }}><img alt={'arrow left'} src={arrowLeft}/> </span>
            }
            {
                pages.filter(p => p >= leftBorderPortion && p <= rightBorderPortion)
                    .map(p => {
                        return (<span key={p}
                                      onClick={() => {
                                          onPageChanged(p)
                                      }}
                                      className={s.pageItem + ' ' + (currentPage === p ? s.selectedPageItem : '')}>{p}</span>)
                    })
            }
            {
                portionNumber < portionsCount && <span className={s.arrowsButton} onClick={() => {
                    setPortionNumber(portionNumber + 1)
                }}><img  alt={'arrow right'} src={arrowRight}/></span>
            }
        </div>
    )
}