import React, {FC} from "react";
import s from './FieldComponents.module.css';
import {FieldValidatorType} from "../../../redux/utilities/validators";
import {Field, WrappedFieldProps} from "redux-form";

const FieldComponent = (Element: any) => {
    return ({input, meta, ...props}: any) => {
        const hasError = meta.error && meta.touched;
        return (
          <div className={s.textareaForm + ' ' + (hasError ? s.error : '')}>
              <div>
                  <Element {...input} {...props} placeholder={props.placeholder}></Element>{props.text}
              </div>
              <span>{hasError ? meta.error : ''}</span>
          </div>
        )
    }

}

export function createField<FormKeysType extends string>(placeholder: string | undefined,
  name: FormKeysType,
  validators: Array<FieldValidatorType>,
  component: FC<WrappedFieldProps>,
  props = {}, text = "") {
    return (<div>
        <Field placeholder={placeholder}
               name={name}
               validate={validators}
               component={component}
               text={text}
               {...props}/>
    </div>)
}


export const Textarea = FieldComponent('textarea')
export const Input = FieldComponent('input');


