import {DefaultResponseType, GetItemsType, instance} from "./api";
import {UsersType} from "../types/types";
import {FilterType} from "../redux/reducers/users_reducer";

export const usersAPI = {
    getUsers(currentPage: number, filter: FilterType = {term: '', friend: null}, pageSize: number) {
        const filterFriend = String(filter.friend)
        let url = `users?page=${currentPage}&count=${pageSize}`
        if(filter.term && !filterFriend) {
            url = `users?page=${currentPage}&count=${pageSize}&term=${filter.term}`
        }
        if(!filter.term && filterFriend) {
            url = `users?page=${currentPage}&count=${pageSize}&friend=${filterFriend}`
        }
        if(filter.term && filterFriend) {
            url = `users?page=${currentPage}&count=${pageSize}&friend=${filterFriend}&term=${filter.term}`
        }

        return instance.get<GetItemsType<UsersType>>(url)
            .then(response => response.data)
    },
    unfollow(userId: number) {
        return instance.delete<DefaultResponseType>(`follow/${userId}`)
            .then(response => response.data)
    },
    follow(userId: number) {
        return instance.post<DefaultResponseType>(`follow/${userId}`, {})
            .then(response => response.data)
    }
}