import React from "react";
import s from '../Users.module.css';
import userPhoto from '../../../../assets/images/user.png';
import {NavLink} from "react-router-dom";
import {UsersType} from "../../../../types/types";
import {useDispatch} from "react-redux";
import {followThunkCreator, unfollowThunkCreator} from "../../../../redux/reducers/users_reducer";

type PropsType = {
    user : UsersType,
    followingProgress: Array<number>
    isAuth: boolean,
}

const User: React.FC<PropsType> = ({user, followingProgress, ...props} ) => {
    const dispatch = useDispatch()
    const follow = (userId: number) => {
        dispatch(followThunkCreator(userId))
    }
    const unfollow = (userId: number) => {
        dispatch(unfollowThunkCreator(userId))
    }

    return (
      <div className={s.user} key={user.id}>
                      <span className={s.userButtons}>
                          <div>
                              <NavLink to={`/profile/${user.id}`}>
                              <img className={s.userPhoto}
                                   alt={'userPhoto'}
                                   src={user.photos.small != null ? user.photos.small : userPhoto}/>
                                  </NavLink>
                          </div>
                          {
                              props.isAuth ?
                                <div>
                                    {user.followed ?
                                      <button disabled={followingProgress
                                        .some(id => id === user.id)} onClick={() => {
                                          unfollow(user.id)
                                      }}>follow</button> :
                                      <button disabled={followingProgress
                                        .some(id => id === user.id)} onClick={() => {
                                          follow(user.id)
                                      }}>unfollow</button>}
                                </div> : ''
                          }
                      </span>
          <div className={s.userInfo}>
              <div className={s.userInfoLeft}>
                  <div className={s.userName}>
                      {user.name}
                  </div>
                  <div>
                      {user.status}
                  </div>
              </div>
              <div className={s.userInfoRight}>
                  {/*{user.country}*/}
              </div>
          </div>
      </div>
    )
}

export default User;