import {ResultCodeEnum, ResultCodeForCaptchaEnum} from "../../api/api"
import {stopSubmit} from "redux-form"
import {authAPI} from "../../api/authAPI";
import {securityAPI} from "../../api/securityAPI";
import {BaseThunkType, InferActionsTypes} from "../redux_store";


let initialState = {
    userId: null as null | number,
    email: null as null | string,
    login: null as null | string,
    isAuth: false as boolean,
    captchaUrl: null as null | string,
}
export type InitialStateAuthType = typeof initialState
export type AuthThunkType = BaseThunkType<ACTIONS | ReturnType<typeof stopSubmit>>
export type LoginFormType = {
    email: null | string
    password: null | string
    rememberMe: true | false
}
type ACTIONS = InferActionsTypes<typeof actions>

const authReducer = (state = initialState, action: ACTIONS): InitialStateAuthType => {
    switch (action.type) {
        case 'auth/SET_USER_DATA':
            return {
                ...state,
                ...action.payload,
            }
        case 'auth/SET-CAPTCHA-URL':
            return {
                ...state,
                captchaUrl: action.payload.captchaUrl
            }
        default:
            return state;
    }
}

export const actions = {
    setAuthUserData: (userId: number | null, email: string | null, login: string | null, isAuth: boolean) =>{
        return ({type: 'auth/SET_USER_DATA', payload: {userId, email, login, isAuth}} as const)
    },
    setCaptchaUrl: (captchaUrl: string | null) => ({type: 'auth/SET-CAPTCHA-URL', payload: {captchaUrl}} as const)
}



export const getAuthUserThunkCreator = (): AuthThunkType => async (dispatch) => {
    let data = await authAPI.authMe()
    if (data.resultCode === ResultCodeEnum.Success) {
        let {id, email, login} = data.data;
        dispatch(actions.setAuthUserData(id, email, login, true));
    }
}

export const loginAuthUserThunkCreator = (formData: LoginFormType): AuthThunkType => async (dispatch) => {
    const {email, password} = formData
    let data = await authAPI.login(email, password)
    console.log(formData)
    if (data.resultCode === ResultCodeEnum.Success) {
        dispatch(actions.setCaptchaUrl(null))
        await dispatch(getAuthUserThunkCreator())
    } else {
        if (data.resultCode === ResultCodeForCaptchaEnum.Captcha) {
            let data = await securityAPI.getCaptcha()
            if (data.url) {
                dispatch(actions.setCaptchaUrl(data.url))
            }
        } else {
            let errorMessage = data.messages.length > 0 ? data.messages[0] : 'Some error'
            dispatch(stopSubmit('login', {_error: errorMessage}))
        }
    }
}

export const logoutAuthUserThunkCreator = (): AuthThunkType => async (dispatch) => {
    let data = await authAPI.logout()
    if (data.resultCode === ResultCodeEnum.Success) {
        dispatch(actions.setAuthUserData(null, null, null, false))
        dispatch({type: 'asdasdasd'})
    }
}

export default authReducer