import React, {FC} from 'react';
import s from "./Header.module.css";
import {Link} from "react-router-dom";
import {Avatar, Button, Col, Menu, Row} from "antd";
import {logoutAuthUserThunkCreator} from "../../redux/reducers/auth_reducer";
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "../../redux/redux_store";
import {Header} from "antd/es/layout/layout";
import {UserOutlined} from "@ant-design/icons";


const AppHeader: FC = () => {
    const isAuth = useSelector((state: AppStateType) => state.auth.isAuth)
    const login = useSelector((state: AppStateType) => state.auth.login)
    const dispatch = useDispatch()
    return (
        <Header className="header">
            {/*<div className="logo">logo</div>*/}
            <Row>
                <Col span={18}>
                    <Menu selectedKeys={[]} theme='dark' mode="horizontal">
                        <Menu.Item key="1"><Link to={'/profile'}>LOGO</Link></Menu.Item>
                        <Menu.Item key="2"><Link to={'/profile'}>Profile</Link></Menu.Item>
                        <Menu.Item key="3"><Link to={'/users'}>Developers</Link></Menu.Item>
                    </Menu>
                </Col>
                <Col span={6}>
                    <div className={s.loginBlock}>
                        {isAuth ?
                            <div>
                                <span>{`${login}`}</span>
                                <Avatar className={s.header__avatar} style={{ backgroundColor: '#87d068' }} icon={<UserOutlined />} />
                                <Button type={'primary'}
                                        onClick={() => dispatch(logoutAuthUserThunkCreator())}>Logout</Button>
                            </div>
                            : <Link to='/login'>Login</Link>}
                    </div>
                </Col>
            </Row>
        </Header>)
}

export default AppHeader;