import {DefaultResponseType, instance, ResultCodeEnum, ResultCodeForCaptchaEnum} from "./api";

type LoginResponseType = {
    id: number
    email: string
    login: string
}

export const authAPI = {
    authMe() {
        return instance.get<DefaultResponseType<LoginResponseType>>(`auth/me`)
            .then(response => response.data)
    },
    login(email: string | null , password: string | null) {
        return instance.post<DefaultResponseType<{}, ResultCodeEnum | ResultCodeForCaptchaEnum>>(`auth/login`, {
            email, password,
        })
            .then(response => response.data)
    },
    async logout() {
        let response = await instance.delete<DefaultResponseType>(`auth/login`)
        return response.data
    },

}