import {InferActionsTypes} from "../redux_store";

type ACTIONS = InferActionsTypes<typeof actions>

export type DialogType = {
    id: number
    name: string
}
export type MessageType = {
    id: number
    message: string
    from: number
    to: number
    avatar: string
}

const initialState = {
    dialogs: [
        {id: 1, name: 'Vlad',},
        {id: 2, name: 'Matvei',},
        {id: 3, name: 'Larisa',},
        {id: 4, name: 'Ulya',},
        {id: 5, name: 'Vitalya',},
        {id: 6, name: 'Natasha',},
    ] as Array<DialogType>,
    messages: [
        {
            id: 1,
            message: "Hello!",
            from: 1,
            to: 0,
            avatar: "https://www.diethelmtravel.com/wp-content/uploads/2016/04/bill-gates-wealthiest-person.jpg",
        },
        {
            id: 0,
            message: "Hello!",
            from: 0,
            to: 1,
            avatar: "https://upload.wikimedia.org/wikipedia/commons/a/a0/Pierre-Person.jpg",
        },
        {
            id: 1,
            message: "How are you?",
            from: 1,
            to: 0,
            avatar: "https://www.diethelmtravel.com/wp-content/uploads/2016/04/bill-gates-wealthiest-person.jpg",
        },
        {
            id: 0,
            message: "I'm fine! Thank you!!",
            from: 0,
            to: 1,
            avatar: "https://upload.wikimedia.org/wikipedia/commons/a/a0/Pierre-Person.jpg",
        },
        {
            id: 1,
            message: "Good bye!",
            from: 1,
            to: 0,
            avatar: "https://www.diethelmtravel.com/wp-content/uploads/2016/04/bill-gates-wealthiest-person.jpg",
        },
        {
            id: 0,
            message: "Bye!!!",
            from: 0,
            to: 1,
            avatar: "https://upload.wikimedia.org/wikipedia/commons/a/a0/Pierre-Person.jpg",
        },
    ] as Array<MessageType>
}
export type InitialStateDialogsType = typeof initialState;

const dialogsReducer = (state = initialState, action: ACTIONS):InitialStateDialogsType  => {

    switch (action.type) {
        case "dialogs/SEND_MESSAGE":
            let message: MessageType = {
                id: 0,
                message: action.payload.textMessage,
                from: 0,
                to: 1,
                avatar: "https://www.diethelmtravel.com/wp-content/uploads/2016/04/bill-gates-wealthiest-person.jpg",
            }
            if (message.message === undefined) {
                return state;
            }
            return {
                ...state,
                messages: [...state.messages, message],
            }
        default:
            return {...state};


    }
}

export const actions = {
    sendMessage: (textMessage: string) => ({type: 'dialogs/SEND_MESSAGE', payload: {textMessage}})
}
export const {sendMessage} = actions


export default dialogsReducer;