import {ResultCodeEnum} from "../../api/api";
import {UsersType} from "../../types/types";
import {ThunkAction} from "redux-thunk";
import {InferActionsTypes} from "../redux_store";
import {usersAPI} from "../../api/usersAPI";

let InitialState = {
    users: [] as Array<UsersType>,
    pageSize: 5 as number,
    totalUsersCount: 0 as number,
    currentPage: 1 as number,
    isLoading: true as boolean,
    followingProgress: [] as Array<number>, // array of users ids
    filter: {
        term: '' as string | null,
        friend: null as boolean | null
    }
}

type InitialStateUsersType = typeof InitialState

const usersReducer = (state = InitialState, action: ActionsTypes): InitialStateUsersType => {
    switch (action.type) {
        case 'users/FOLLOW':
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.payload.userID) {
                        return {...u, followed: true}
                    } else {
                        return u;
                    }
                }),
            }
        case 'users/UNFOLLOW':
            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.payload.userID) {
                        return {...u, followed: false}
                    } else {
                        return u;
                    }
                }),
            }
        case 'users/SET_USERS':
            return {
                ...state,
                users: [...action.payload.users],
            }
        case 'users/SET_CURRENT_PAGE':
            return {
                ...state,
                currentPage: action.payload.currentPage,
            }
        case 'users/SET_TOTAL_USERS_COUNT':
            return {
                ...state,
                totalUsersCount: action.payload.totalUsersCount,
            }
        case 'users/TOGGLE_IS_LOADING':
            return {
                ...state,
                isLoading: action.payload.isLoading,
            }
        case 'users/TOGGLE_FOLLOWING_PROGRESS':
            return {
                ...state,
                followingProgress: action.payload.isLoading ?
                    [...state.followingProgress, action.payload.userId] :
                    state.followingProgress.filter(id => id !== action.payload.userId)
            }
        case "users/SET_FILTER":
            return {...state, filter: action.payload.filter}

        default:
            return state;
    }
}
type ActionsTypes = InferActionsTypes<typeof actions>

export const actions = {
    follow: (userID: number) => ({type: 'users/FOLLOW', payload: {userID}} as const),
    unfollow: (userID: number) => ({type: 'users/UNFOLLOW', payload: {userID}} as const),
    setUsers: (users: Array<UsersType>) => ({type: 'users/SET_USERS', payload: {users}} as const),
    setFilter: (filter: FilterType) => ({type: 'users/SET_FILTER', payload: {filter}} as const),
    setCurrentPage: (currentPage: number) => ({type: 'users/SET_CURRENT_PAGE', payload: {currentPage}} as const),
    setTotalUsersCount: (totalUsersCount: number) => ({type: 'users/SET_TOTAL_USERS_COUNT', payload: {totalUsersCount}} as const),
    toggleIsLoading: (isLoading: boolean) => ({type: 'users/TOGGLE_IS_LOADING', payload: {isLoading}} as const),
    toggleFollowingProgress: (isLoading: boolean, userId: number) => ({type: 'users/TOGGLE_FOLLOWING_PROGRESS', payload: {isLoading, userId}} as const)
}


// type DispatchType = Dispatch<ActionsTypes>
// type GetStateType = () => InitialStateUsersType
type ThunkType = ThunkAction<Promise<void>, InitialStateUsersType, unknown, ActionsTypes>

export const getUsersThunkCreator = (currentPage: number, filter: FilterType,  pageSize: number):
    ThunkType => async (dispatch, getState) => {
    dispatch(actions.setCurrentPage(currentPage))
    dispatch(actions.setFilter(filter))
    dispatch(actions.toggleIsLoading(true))
    let data = await usersAPI.getUsers(currentPage, filter, pageSize)
    dispatch(actions.toggleIsLoading(false))
    dispatch(actions.setUsers(data.items))
    dispatch(actions.setTotalUsersCount(data.totalCount));
}

export const unfollowThunkCreator = (userId: number): ThunkType => async (dispatch) => {
    dispatch(actions.toggleFollowingProgress(true, userId))
    let data = await usersAPI.unfollow(userId)
    if (data.resultCode === ResultCodeEnum.Success) {
        dispatch(actions.unfollow(userId))
    }
    dispatch(actions.toggleFollowingProgress(false, userId))
}

export const followThunkCreator = (userId: number): ThunkType => async (dispatch) => {
    dispatch(actions.toggleFollowingProgress(true, userId))
    let data = await usersAPI.follow(userId)
    if (data.resultCode === ResultCodeEnum.Success) {
        dispatch(actions.follow(userId))
    }
    dispatch(actions.toggleFollowingProgress(false, userId))
}

export type FilterType = (typeof InitialState.filter)

export default usersReducer;