import {MessageType} from "../components/pages/Chat/ChatPage";
type EventNameStatusType = 'event-changed' | 'messages-received'
export type WSSStatusType = 'pending' | 'ready' | 'error'
type ChatAPICallbackType = (messages: MessageType[]) => void
type EventNameSubscribersType = (status: WSSStatusType) => void

let ws: WebSocket

const onCloseWsChanel = () => {
    subscribers['event-changed'].forEach(s => s('pending'))
    setTimeout(onCreateWsChanel, 3000)
}

const cleaUp = () => {
    ws?.removeEventListener('close', onCloseWsChanel)
    ws?.removeEventListener('message', onMessageHandler)
    ws?.removeEventListener('error', onErrorWsChanel)
    ws?.removeEventListener('open', onOpenWsChanel)
    ws?.close()
}

function onErrorWsChanel() {
    subscribers['event-changed'].forEach(s => s('error'))
}

function onOpenWsChanel() {
    subscribers['event-changed'].forEach(s => s('ready'))
}


const onCreateWsChanel = () => {
    cleaUp()
    ws = new WebSocket('wss://social-network.samuraijs.com/handlers/ChatHandler.ashx')
    ws.addEventListener('close', onCloseWsChanel)
    ws.addEventListener('error', onErrorWsChanel)
    ws.addEventListener('open', onOpenWsChanel)
    ws.addEventListener('message', onMessageHandler)
}


const onMessageHandler = (e: MessageEvent) => {
    const newMessages = JSON.parse(e.data)
    subscribers['messages-received'].forEach(s => s(newMessages))
}

let subscribers = {
    'messages-received' : [] as ChatAPICallbackType[],
    'event-changed': [] as EventNameSubscribersType[]
}

export const chatAPI = {
    start: () => {
        onCreateWsChanel()
    },
    stop: () => {
        subscribers['messages-received'] = []
        subscribers['event-changed'] = []
        cleaUp()
    },
    subscribe: (eventName: EventNameStatusType, callback: ChatAPICallbackType | EventNameSubscribersType) => {
        // @ts-ignore
        subscribers[eventName].push(callback)
    },
    unsubscribe: (eventName: EventNameStatusType, callback: ChatAPICallbackType| EventNameSubscribersType) => {
        // @ts-ignore
        subscribers[eventName].filter(s => s !== callback)
    },
    sendMessage: (message: string) => {
        ws?.send(message)
    }

}