import React, {FC} from 'react'
import {Field, InjectedFormProps, reduxForm} from "redux-form";
import {createField, Input} from "../../common/FieldComponents/FieldComponents";
import {maxLength, required} from "../../../redux/utilities/validators";
import s from '../../common/FieldComponents/FieldComponents.module.css'
import {LoginFormType} from "../../../redux/reducers/auth_reducer";

const maxlength40 = maxLength(40);
const maxlength10 = maxLength(10);

type LoginFormPropsType = {
    captchaUrl: string | null
}

export const LoginForm: FC<LoginFormPropsType & InjectedFormProps<LoginFormType, LoginFormPropsType>> = (props) => {
    return (
        <>
            <form onSubmit={props.handleSubmit}>
                <div>
                    {createField<LoginFormFieldTypeKeys>('login', 'email', [required, maxlength40], Input)}
                </div>
                <div>
                    {createField<LoginFormFieldTypeKeys>('password', 'password', [required, maxlength40], Input)}
                </div>
                <div>
                    {createField<LoginFormFieldTypeKeys>(undefined, 'rememberMe', [], Input, {type: 'checkbox'}, 'Remember me')}
                </div>
                {props.error && <div className={s.formSummaryError}>
                    {props.error}
                </div>}
                {
                    props.captchaUrl ? <Captcha captchaUrl={props.captchaUrl}/> : ''
                }


                <div>

                    <button type={'submit'}>Login</button>
                </div>

            </form>
        </>
    )
}
export const Captcha: FC<{ captchaUrl: string }> = (props) => {
    return (
        <div>
            <div><img alt={'captcha'} src={props.captchaUrl}/></div>
            {createField<CaptchaFormFieldKeys>(undefined, 'captcha', [required, maxlength10], Input)}
        </div>
    )
}

export const ReduxLoginForm = reduxForm<LoginFormType, LoginFormPropsType>({
    form: 'login',
})(LoginForm)

export default ReduxLoginForm;


type LoginFormFieldTypeKeys = Extract<keyof LoginFormType, string>
type CaptchaFormFieldKeys = Extract<keyof { captcha: string }, string>

export function myField<T extends string>(name: T) {
    return (<Field name={name} type='text' component={'input'}/>)

}