import {Action, applyMiddleware, combineReducers, compose, createStore} from "redux";
import profileReducer from "./reducers/profile_reducer";
import dialogsReducer from "./reducers/dialogs_reducer";
import sidebarReducer from "./reducers/sidebar_reducer";
import usersReducer from "./reducers/users_reducer";
import thunkMiddleware, {ThunkAction} from "redux-thunk";
import {reducer as formReducer} from 'redux-form';
import appReducer from "./reducers/app_reducer";
import authReducer from "./reducers/auth_reducer";
import chatReducer from "./reducers/chat_reducer";

let rootReducer = combineReducers({
    app: appReducer,
    profilePage: profileReducer,
    dialogsPage: dialogsReducer,
    sidebar: sidebarReducer,
    usersPage: usersReducer,
    auth: authReducer,
    form: formReducer,
    chat: chatReducer,
})

export type RootReducerType = typeof rootReducer // (globalstate: GLOBALSTATE) => GLOBALSTATE
export type AppStateType = ReturnType<RootReducerType>


//GENERIC РАЗОБАРТЬСЯ для ActionTypes
export type InferActionsTypes<T> = T extends { [key: string]: (...args: any[]) => infer U} ? U : never
export type BaseThunkType<A extends Action, R = Promise<void>> = ThunkAction<R, AppStateType, null, A>

// @ts-ignore
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(rootReducer, /* preloadedState, */ composeEnhancers(
  applyMiddleware(thunkMiddleware)
));

// @ts-ignore
window.store = store;

export default store;

